import { getTagNameWithoutPrefix, throwException } from '../../../utils';
import { forceUpdate } from '@stencil/core';
export const STEPPER_HORIZONTAL_SIZES = ['small', 'medium'];
export const getIndexOfStepWithStateCurrent = (stepperHorizontalItems) => {
    return stepperHorizontalItems.findIndex((item) => item.state === 'current');
};
export const throwIfMultipleCurrentStates = (host, stepperHorizontalItems) => {
    const currentStateCount = stepperHorizontalItems.filter((item) => item.state === 'current').length;
    if (currentStateCount > 1) {
        throwException(`only one child with current state is allowed in ${getTagNameWithoutPrefix(host)} but got ${currentStateCount}.`);
    }
};
export const syncStepperHorizontalItemsProps = (host, theme) => {
    Array.from(host.children).forEach((item) => {
        item.theme = theme;
        forceUpdate(item);
    });
};
